<template>
  <div>
    <div
      v-for="(exemptionList, exemptionType) in exemptions"
      :key="`${(exemptionType || '').toLowerCase()}-exemptions-table`"
      class="mb-4"
    >
      <h5 class="mb-1">{{ exemptionType[0].toUpperCase() + exemptionType.toLowerCase().slice(1) }} Exemptions</h5>

      <BTable tbody-tr-class="align-middle" :fields="fields" :items="exemptionList" responsive>
        <template #cell(ref)="data">
          <span class="fw-bold">
            {{ data.value.toUpperCase() }}
          </span>
        </template>
        <template #cell(previousExemptions)="data">
          <div class="w-100 d-flex flex-column">
            <div
              v-for="(previousExemption, previousExemptionIndex) in data.value"
              :key="`${exemptionType}-previous-exemption-row-${previousExemptionIndex}`"
              :class="[
                'w-100 d-flex text-start flex-column flex-md-row',
                'align-items-start align-items-md-center',
                {
                  'border-bottom border-success': previousExemptionIndex < data.value.length - 1,
                  'pb-2': previousExemptionIndex === 0,
                  'py-2': previousExemptionIndex > 0 && previousExemptionIndex < data.value.length - 1,
                  'pt-2': previousExemptionIndex === data.value.length - 1
                }
              ]"
            >
              <div class="fw-bold fw-md-normal text-start" style="min-width: 60px">
                Part {{ previousExemption.ref }}
              </div>
              <div class="ms-md-3 flex-fill flex-shrink-1">{{ previousExemption.description }}</div>
            </div>
          </div>
        </template>
      </BTable>
    </div>
  </div>
</template>

<script setup>
  import exemptions from "@gotrash/uk-waste-exemptions";

  const fields = [
    {
      key: "ref",
      tdClass: "font-weight-bold",
      formatter: value => value.toUpperCase()
    },
    {
      key: "description"
    },
    {
      key: "previousExemptions"
    }
  ];
</script>
